<template>
    <div class="banner">
        <!-- swiper -->
         <swiper :options="swiperOption">
        <swiper-slide><img src="https://img11.360buyimg.com/cms/jfs/t27130/79/2126883302/187506/3d1437ba/5bf79c47Nfc4e0003.jpg"></swiper-slide>
        <swiper-slide><img src="https://img12.360buyimg.com/cms/jfs/t30130/232/1045514436/355621/b9f1b6a0/5c06156dN2a1ecd7a.jpg"></swiper-slide>
        <swiper-slide><img src="https://img12.360buyimg.com/cms/jfs/t26953/181/2232523153/99764/888557c8/5bfd05efN90cf1902.jpg"></swiper-slide>
        <swiper-slide><img src="https://img14.360buyimg.com/cms/jfs/t30460/118/748922260/280267/89f19f23/5bfce63fN20e0acb5.jpg"></swiper-slide>
        <swiper-slide><img src="https://img12.360buyimg.com/cms/jfs/t1/9500/6/7950/62103/5c0904f6E37da3c23/ee5e1a9734686c3e.jpg"></swiper-slide>
        
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
      </swiper>
    </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default{
    name:'Banner',
    components:{
        swiper,
        swiperSlide
    },
    data(){
        return{
                swiperOption: {
                    slidesPerView: 1,
                    spaceBetween: 30,
                    loop: true,
                    autoplay:1000,
                    pagination: {
                        el: '.swiper-pagination',
                        clickable: true
                    },
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev'
                    }
                }
        }
    }
}
</script>
<style scoped>
.banner{
    width:100%;
    /* overflow: hidden; */
}
.swiper{
     touch-action: none;
     width:100%;
     height: 100%;
}
.swiper-slide img{
    border:none;
    width:100%;
    
}
.swiper-pagination-bullet{
    background-color: #fff !important;
}
.swiper-button-prev,.swiper-button-next{
    outline: none;
}
</style>



