<template>
    <div class="per">
        <div class="person">
            <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1544155087&di=8285f7b8ffe71dd5feb2a8ef27e337a5&imgtype=jpg&er=1&src=http%3A%2F%2Fmail.albiz.cn%2FimgMsg88%2Fimages%2F201708%2F15029320689934.jpg">
            <div class="person-mark">
                <div class="image">
                        <img src="http://img.52z.com/upload/news/image/20180620/20180620113503_47743.jpg">
                </div>
                <p>{{perName}}</p>
            </div>
        </div>
        <div class="con">
            <div class="con-per">
                <router-link to="/persondata" @click="toData()"><div>设置个人资料<Icon type="ios-arrow-forward" /></div></router-link>
                <router-link to="/address"><div>设置地址管理<Icon type="ios-arrow-forward" /></div></router-link>
            </div>
            <div class="con-mine">
                <router-link to=""><div>我的订单<Icon type="ios-arrow-forward" /></div></router-link>
                <router-link to=""><div>我的评价<Icon type="ios-arrow-forward" /></div></router-link>
            </div>
            <div class="con-shop">
                <router-link to=""><div>商品收藏<Icon type="ios-arrow-forward" /></div></router-link>
                <router-link to=""><div>店铺收藏<Icon type="ios-arrow-forward" /></div></router-link>
                <router-link to=""><div>浏览记录<Icon type="ios-arrow-forward" /></div></router-link>
            </div>
        </div>
        <button type="button" class="exit" @click="exit">退出登录</button>
        <router-view></router-view>
    </div>
</template>
<script>
export default {
    name:'Person',
    data(){
        return{
            perName:'追风少年',
        }
    },
    mounted(){
        if(sessionStorage.isLogin){
            this.perName = JSON.parse(sessionStorage.isLogin);
        }
    },
    methods:{
        exit(){
            this.$emit('edit');
        },
        toData(){
            this.$store.dispatch('changeLoginState', true);
        }
    }
}
</script>
<style scoped>
    .person{
        width:100%;
        height: 200px;
        position: relative;
        overflow: hidden;
    }
    .person>img{
        width:100%;
        opacity: 0.5;
    }
    
    .person-mark{
        position: absolute;
        top:50%;
        left:50%;
        width:50%;
        transform: translate(-50%,-50%);
        
    }
    .person-mark .image{
        margin: 0 auto;
        border-radius: 50%;
        width:130px;
        height:130px;
        overflow: hidden;
    }
    .person-mark .image img{
        width:100%;
        height: 100%;
    }
    .person-mark p{
        padding-top:10px;
        font-size: 16px;
        color:#f55;
        text-shadow: 1px 1px 1px #666;
    }
    .con{
        padding:10px 0;
        width:100%;
        background-color: #eee;
    }
    .con-per,.con-mine,.con-shop{
        margin-bottom:10px;
        width:100%;
        background-color: #fff;
    }
    .con-per div,.con-mine div,.con-shop div{
        width:100%;
        height: 50px;
        line-height: 50px;
        text-align: left;
        padding-left:10px;
        border-top: 1px solid #ddd;
        position: relative;
        color:#333;
    }
    .con-per>div a,.con-mine>div a,.con-shop>div a{
        display: inline-block;
        text-decoration: none;
        
    }
    .con-per div i,.con-mine div i,.con-shop div i{
        position: absolute;
        top:50%;
        right:10px;
        transform: translateY(-50%);
        font-size: 20px;
        color:#aaa;
    }
    .exit{
        margin: 20px 0;
        width:calc(100% - 10%);
        height: 50px;
        background-color: rgb(168, 9, 9);
        color:#fff;
        line-height: 50px;
        outline: none;
        border:none;
        border-radius: 5px;
    }
</style>


