<template>
    <div class="detail">
        <div class="header"> 
            <div class="back" @click="goBack"><Icon type="ios-arrow-back" /></div>
            <div class="title">图书详情</div>
        </div>
        <div class="con">
            <div class="book-name">{{books.name}}</div>
            <div class="book-box">
                <div class="book-left">
                    <img :src="books.cover">
                </div>
                <div class="book-right">
                    <p><span>作者</span><span>{{books.author}}</span></p>
                    <p><span>页数</span><span>{{books.page}}</span></p>
                    <p><span>书号</span><span>{{books.bookNum}}</span></p>
                    <p><span>出版日期</span><span>{{books.date}}</span></p>
                    <p><span>售价</span><span class="price-red">￥{{books.price}}</span></p>
                </div>
            </div>
            <fieldset class="des">
                <legend>本书内容简介</legend>
                <p>{{books.info2}}</p>
            </fieldset>
        </div>
        <div class="footer">
            <div class="add-btn" @click="addBtn()">加入购物车</div>
            <div class="buy-btn">点击购买</div>
        </div>
    </div>
</template>
<script>
export default {
    name:'Detail',
    data(){
        return{
            books:'',
            isPush:false,
            booksArr:[]
        }
    },
    created(){
        let index = this.detData.index;
        let name = this.detData.name;
        let url = this.detData.url;
        // let url = 'book-infos.json';
        this.$axios.get(url, {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
        }).then(res => {
            if(name == "news"){
                this.books = res.data.news[index];
            }else if(name == 'edit'){
                this.books = res.data.edit[index];
            }else if(name == 'hot'){
                this.books = res.data.hot[index];
            }else if(name == 'classify'){
                this.books = res.data[index];
            }
        })
    },
    methods:{
        /* 关闭详情页 */
        goBack(){
            this.$emit('goBack');
        },
        /* 添加到购物车 */
        addBtn(){
            if(sessionStorage.isLogin){
                let booksObj = {
                image:this.books.cover,
                name:this.books.name,
                author:this.books.author,
                price:this.books.price,
                num:1
                }
                if(localStorage.books){
                    this.booksArr = JSON.parse(localStorage.books);
                }
                for(let m = 0; m < this.booksArr.length; m++){
                        if(this.booksArr[m].name == this.books.name){
                            if(this.booksArr[m].num < 5){
                                this.booksArr[m].num += 1;
                            }else{
                                this.$Modal.info({
                                    title: "title",
                                    content: "最多添加5本书!"
                                });
                            }
                            this.isPush = true;
                        }
                    }
                if(!this.isPush){
                    this.booksArr.push(booksObj);
                }
                localStorage.books = JSON.stringify(this.booksArr);
                this.$Modal.success({
                    title: "title",
                    content: "添加成功"
                });
            }else{
                this.$Modal.warning({
                    title: "title",
                    content: "还未登录请登录账号"
                });
                return;
            }
        }
        
    },
    /* 接受父组件中传过来的信息 */
    props:[
        'detData'
    ]
}
</script>
<style scoped>
.detail{
    width:100%;
    height: 100%;
    position: fixed;
    top:0;
    left:0;
    background-color: #fff;
    z-index: 10;
    overflow-y: auto;
    overflow-x: hidden;
}
.header{
    color:#fff;
    font-size: 0;
    height: 64px;
    line-height: 64px;
    position: relative;
}
.header .back{
    position: absolute;
    left:20px;
    top:50%;
    transform: translateY(-50%);
    font-size: 26px;
}
.header .title{
    width:100%;
}
.con{
    overflow-y: auto;
    overflow-x: hidden;
    height:calc(100% - 64px - 49px);
    width:100%;
}
.book-name{
    width:100%;
    text-align: left;
    padding:0 10px;
    font-size: 22px;
    font-weight: bold;
    color:#111;
}
.book-box{
    padding: 10px;
    width:100%;
    font-size: 0;
}
.book-box>div{
    display: inline-block;
}
.book-box .book-left{
    width:35%;
}
.book-box .book-left img{
    width:100%;
}
.book-box .book-right{
    text-align: left;
    width:65%;
    font-size: 16px;
    vertical-align: top;
}
.book-box .book-right p{
    margin:10px 0;
}
.book-box .book-right span:nth-child(1){
    margin:0 10px 0 10px;
    color:#888;
    width:30%;
    display: inline-block;
}
.book-box .book-right span:nth-child(2){
    color:#333;
}
.price-red{
    color:red !important;
}
.des{
    width:95%;
    padding:5px;
    margin: 10px auto;
}
.des legend{
    text-align: left;
    padding-left: 10px;
    color:#888;
    font-weight: normal;
    font-size: 16px;
}
.des p{
    text-align: left;
    letter-spacing: 2px;
    padding:10px;
    color:#333;
    font-size: 14px;
    display:inline-block;
}
.footer{
    width:100%;
    height: 49px;
    background-color: #333;
    box-sizing: border-box;
    line-height: 49px;
}
.footer>div{
    display: inline-block;
    width:49%;
    color:#fff;
    height: 100%;
    cursor: pointer;
}
.add-btn{
    border-right:1px solid #888;
}
</style>



