<template>
    <div class="page mine">
        <Login v-if="hidden" @log="login" @reg="login"/>
        <Personal v-else @edit="edit"/>
    </div>
</template>
<script>
import Login from '../pages/login';
import Personal from '../pages/personal';
export default{
    name:'mine',
    components:{
        Login,
        Personal
    },
    data(){
        return{
            hidden:false
        }
    },
    mounted(){
        if(!sessionStorage.isLogin){
            this.hidden = true;
        }
    },
    methods:{
        // 退出登录
        edit(){
            this.hidden = true;
            sessionStorage.removeItem("isLogin");
        },
        // 登陆到个人中心切换
        login(){
            this.hidden = false;
        }
    }
}
</script>
<style scoped>
    
</style>



