<template>
  <div id="app">
    <div class="header">
      <h1 class="title">{{this.$store.state.title}}</h1>
    </div>
    <router-view/>
    <ul class="tabBar">
      <div class="cursor" :style="cursorObj"></div>
      <li class="tabBarItem" v-for="(item,index) in links" :key="index" @click="tabBar(index)">
        <router-link :to="item.path"><Icon class="icon" :type="item.icon"/>{{item.title}}</router-link>
      </li>
      <div class="curNum" v-show="isCur">{{curNum}}</div>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      links:[
        {title:"主页",path:'/',icon:'ios-home'},
        {title:"搜索",path:'/classify',icon:'md-share'},
        {title:"购物",path:'/shopping',icon:'ios-cart'},
        {title:"我的",path:'/mine',icon:'ios-contact'}
      ],
      cursorObj:{
        left:0
      },
      curNum:'',
      timer:'',
      isCur:false
    }
  },
  computed:{

  },
  mounted(){
    this.timer = setInterval(()=>{
      if(sessionStorage.isLogin){
        this.isCur = true;
      }else{
        this.isCur = false;
      }
      if(localStorage.books){
        this.curNum = JSON.parse(localStorage.books).length;
      }else{
        this.curNum = 0;
      }
    },1000);
    
  },
  methods:{
    tabBar(index){
      if(sessionStorage.isLogin){
        this.cursorObj.left = `${25 * index}%`;
      }else{
        if(index != 2){
          this.cursorObj.left = `${25 * index}%`;
        }
      }
    }
  },
  beforeDestroy(){
    clearInterval(this.timer);
  }

}
</script>

<style>
*{
  margin:0;
  padding:0;
}
html,body{
  height:100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
.header {
  height: 64px;
  background: #333;
  color: #fff;
}
.header > .title {
  line-height: 64px;
  text-align: center;
  font-size: 16px;
}
.page {
  height: calc(100% - 49px - 64px);
  overflow-y: auto;
}
.tabBar {
  margin:0;
  padding:0;
  list-style: none;
  height: 49px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: #333;
}
.tabBarItem {
  width: calc(100% / 4);
  height: 100%;
  z-index: 1;
}
.tabBarItem  a {
  display: inline-block;
  font-size: 12px;
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  text-decoration: none;
}
.cursor {
    width: 25%;
    height: 100%;
    background: orange;
    transition: all .25s linear;
    position: absolute;
    top: 0;
    left: 0;
}
.tabBarItem .icon{
  margin-top:5px;
  font-size: 20px;
  display: block;
}
.tabBar li:nth-of-type(3){
  position: relative;
}
.curNum{
  position: absolute;
  margin-left: 17%;
  top:4px;
  width:15px;
  height: 15px;
  background-color: red;
  border-radius:50%;
  color:#fff;
  z-index: 1;
  line-height: 15px;
}
</style>



