// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import $ from 'jquery'


Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  switch(to.name) {
    case "home":  store.commit('changeTitle', "主页"); break;
    case "classify":  store.commit('changeTitle', "搜索"); break;
    case "shopping":  store.commit('changeTitle', "购物"); break;
    case "mine":  store.commit('changeTitle', "我的"); break;
    case "persondata": store.commit('changeTitle', "个人资料修改"); break;
    case "address": store.commit('changeTitle', "收货地址"); break;
  }
  next();
})

// 导入axios
import Axios from 'axios'
// 将axios挂载到Vue上
Vue.prototype.$axios = Axios
Axios.defaults.baseURL = './static/json/';

import iView from 'iview';
import 'iview/dist/styles/iview.css';
Vue.use(iView);


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



