<template>
    <div class="page home">
        <Banner/>
        <div class="title-infos">
            双十二全场图书八折优惠，还有更多优惠待你来领取
        </div>
        <div class="books-content">
            <div class="books-news">
                <h3 class="books-title">新书上架</h3>
                <div class="books-box" v-for="(book,index) in books.news" :key="index" @click="newsBtn(index)">
                    <div class="image-box">
                        <img :src="book.cover" :alt="book.name"/>
                    </div>
                    <p class="books-name">{{book.name}}</p>
                    <p class="books-price">￥ {{book.price}}</p>
                </div>
            </div>
            <div class="books-edit">
                <h3 class="books-title">新书上架</h3>
                <div class="books-box" v-for="(book,index) in books.edit" :key="index" @click="editBtn(index)">
                    <div class="image-box">
                        <img :src="book.cover" :alt="book.name"/>
                    </div>
                    <p class="books-name">{{book.name}}</p>
                    <p class="books-price">￥ {{book.price}}</p>
                </div>
            </div>
            <div class="books-hot">
                <h3 class="books-title">新书上架</h3>
                <div class="books-box" v-for="(book,index) in books.hot" :key="index" @click="hotBtn(index)">
                    <div class="image-box">
                        <img :src="book.cover" :alt="book.name"/>
                    </div>
                    <p class="books-name">{{book.name}}</p>
                    <p class="books-price">￥ {{book.price}}</p>
                </div>
            </div>
        </div>
        <Detail v-if="isDetail" :detData="detIndex" @goBack="goback()"/>
    </div>
</template>
<script>
import Banner from '../components/banner';
import router from 'vue-router';
import Detail from './detail';

export default{
    name:'home',
    components:{
        Banner,
        Detail
    },
    data(){
        return{
            books:'',
            isDetail:false,
            detIndex:''
        }
    },
    created(){
        let url = 'book-infos.json';
        this.$axios.get(url, {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
        }).then(res => {
            this.books = res.data;
        })
        
    },
    methods:{
        /* 显示新的详情页 */
        newsBtn(index){
            // this.$router.push({path: 'detail', query: {news: index}});
            this.isDetail = true;
            this.detIndex = {name:"news",index,url:'book-infos.json'};
        },
        /* 显示编辑的详情页 */
        editBtn(index){
            this.isDetail = true;
            this.detIndex = {name:"edit",index,url:'book-infos.json'};
        },
        /* 显示热点的详情页 */
        hotBtn(index){
            this.isDetail = true;
            this.detIndex = {name:"hot",index,url:'book-infos.json'};
        },
        goback(){
            this.isDetail = false;
        }
    }
}
</script>
<style scoped>
    .title-infos{
        padding:5px 0;
        width:100%;
        color:#333;
        font-size:12px;
        border-bottom: 1px solid #ddd;
    }
    .books-content{
        width:100%;
        text-align: left;
    }
    .books-news, .books-edit, .books-hot{
        width:100%;
        overflow: hidden;
    }
    .books-title{
        padding: 5px 0;
        width:100%;
        text-align: left;
        padding-left:10px;
        font-weight: 600;
    }
    .books-box{
        margin: 0 8px 20px;
        padding:5px;
        width:45%;
        display: inline-block;
        vertical-align: top;
        box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1);
        box-sizing: border-box;
        text-align: left;
    }
    .image-box{
        width:100%;
        height: 200px;
    }
    .image-box img{
        width:100%;
        height: 100%;
    }
    .books-name{
        margin: 5px;
        
        width:100%;
        font-size: 14px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .books-price{
        margin-bottom: 10px;
        color:red;
        font-size: 14px;
    }
</style>



