<template>
    <div class="page address">
        <Icon type="ios-arrow-back" class="goBack" @click="goBack()"/>
        <div class="con">
            <div class="con-box">
                <span>姓名：</span><input type="text"  placeholder="请填写您的真实姓名">
            </div>
            <div class="con-box">
               <span>联系电话：</span><input type="text" :value="personData.num" placeholder="请填写我们联系您的电话号码">
            </div>
            <div class="con-box">
               <span>邮政编码：</span><input type="text"  placeholder="请填写您所在地区的邮政编码">
            </div>
            <div class="con-box">
               <span>详细地址：</span><input type="text"  placeholder="请填写详细的街道、楼栋、门牌号">
            </div>
            <div class="con-box">
                <select name="" id="province">
                    <option value="四川省">四川省</option>
                    <option value="广州省">广州省</option>
                    <option value="广西省">广西省</option>
                    <option value="浙江省">浙江省</option>
                    <option value="福建省">福建省</option>
                    <option value="...">...</option>
                </select>
                <select name="" id="city">
                    <option value="成都市">成都市</option>
                    <option value="绵阳市">绵阳市</option>
                    <option value="攀枝花市">攀枝花市</option>
                    <option value="泸州市">泸州市</option>
                    <option value="广元市">广元市</option>
                    <option value="...">...</option>
                </select>
                <select name="" id="area">
                    <option value="武侯区">武侯区</option>
                    <option value="金牛区">金牛区</option>
                    <option value="高新区">高新区</option>
                    <option value="锦江区">锦江区</option>
                    <option value="青江区">青江区</option>
                    <option value="...">...</option>
                </select>
            </div>
            <button type="button" class="modify">确认地址</button>
        </div>
    </div>
</template>
<script>
export default {
    name:'Address',
    data(){
        return{
            personData:''
        }
    },
    created(){
        let data = JSON.parse(sessionStorage.booksUser);
        let isLoginData = JSON.parse(sessionStorage.isLogin);
        for(let i = 0; i < data.length; i++){
            if(data[i].name == isLoginData){
                this.personData = data[i];
            }
        }
    },
    mounted(){
        $('.tabBar').css('display','none');
    },
    methods:{
        goBack(){
            this.$router.go(-1);
            $('.tabBar').css('display','');
        }
    }
}
</script>
<style scoped>
.header{
    color:#fff;
    font-size: 0;
    height: 64px;
    line-height: 64px;
    position: relative;
}
.goBack{
    position: absolute;
    left:10px;
    top:20px;
    color:#fff;
    font-size: 20px;
}
.header .back{
    position: absolute;
    left:20px;
    top:50%;
    transform: translateY(-50%);
    font-size: 26px;
}
.header .title{
    width:100%;
}
.address{
    width:100%;
    
    background-color: #eee;
    height:calc(100% - 64px);
}
.con{
    height:calc(100% - 64px);
    width:100%;
}
.con-box{
    text-align: left;
    width:100%;
    height: 50px;
    border-bottom:1px solid #ccc;
    line-height: 50px;
    background-color: #fff;
}
.con-box input{
    letter-spacing: 2px;
    border:none;
    outline: none;
}
.con-box input[type='text'],.con-box input[type='num']{
    margin-left: 10px;
    width:calc(100% - 100px);
    height: 99%;
}
.con-box span{
    display: inline-block;
    width:60px;
    margin-left: 20px;
    color:#888;
}
.con-box:nth-child(5){
    text-align: center;
}
.con-box select{
    margin: 0 1%;
    width:30%;
    height: 30px;
    outline: none;
}
.con-des{
    padding:15px 0;
    width:100%;
    text-align: left;
    border-bottom:1px solid #ccc;
    background-color: #fff;
}
.con-des span{
    vertical-align: top;
    display: inline-block;
    width:60px;
    margin-left: 20px;
    color:#888;
}

.modify{
    margin: 30px 0;
    width:calc(100% - 20px);
    height: 50px;
    line-height: 50px;
    background-color: rgb(11, 102, 11);
    color:#fff;
    border:none;
    outline: none;
    border-radius: 5px;
    font-size: 16px;
}
</style>




