<template>
    <div class=" loginReg-box">
        
        <div class="login-box">
            <div class="login-title">
                <span @click="switchLogin()" :class="{'border':isBorder}">登录</span><span  @click="switchReg()" :class="{'border':isRegBodrder}">注册</span>
            </div>
            <div class="input log-status" v-show="isLogin" key="login">
                <div>
                    <Icon type="md-phone-portrait" />
                    <input type="number" placeholder="请输入您的手机号" v-model="inputNum" key="log">
                    
                </div>
                <div>
                    <Icon type="md-lock" />
                    <input type="password" placeholder="请输入您的密码" v-model="inputPass" key="log1">
                </div>
                <div>
                    <button type="button" @click="log()">登陆</button>
                </div>
            </div>
            <div class="input reg-status" v-show="isReg" key="reg">
                <div>
                    <Icon type="md-phone-portrait" />
                    <input type="number" placeholder="请输入您的11位手机号" v-model="inputRegNum" key="reg">
                </div>
                <div>
                    <Icon type="md-contact" />
                    <input type="text" placeholder="请输入您的昵称" v-model="inputRegName" key="reg1">
                </div>
                <div>
                    <Icon type="ios-lock-outline" />
                    <input type="password" placeholder="请输入您的密码且长度不少于6位" v-model="inputPass1" key="reg2">
                </div>
                <div>
                    <Icon type="md-lock" />
                    <input type="password" placeholder="请确认您的密码" v-model="inputPass2" key="reg3">
                </div>
                <div>
                    <button type="button" @click="reg()">注册</button>
                </div>
            </div>
        </div>
       
    </div>
</template>
<script>
export default{
    name:'Login',
    components:{
    },
    data(){
        return{
            isLogin:true,
            isReg:false,
            isBorder:true,
            isRegBodrder:false,
            inputNum:'',
            inputPass:'',
            inputRegNum:'',
            inputRegName:'',
            inputPass1:'',
            inputPass2:''
        }
    },
    methods:{
        /* 切换到登陆页面 */
        switchLogin(){
            this.isLogin = true;
            this.isReg = false;
            this.isBorder = true;
            this.isRegBodrder = false;
        },
        /* 切换到注册页面 */
        switchReg(){
            this.isLogin = false;
            this.isReg = true;
            this.isBorder = false;
            this.isRegBodrder = true;
        },
        /* 点击注册 */
        reg(){
            if(this.inputRegNum == '' || this.inputRegName == '' || this.inputPass1 == '' || this.inputPass2 == ''){
                this.$Modal.warning({
                    title: "title",
                    content: "请完善注册信息"
                });
                return;
            }
            if(this.inputRegNum.length != 11 || this.inputPass1.length < 6){
                this.$Modal.warning({
                    title: "title",
                    content: "手机不等于11位或者密码少于6位"
                });
                return;
            }
            if(sessionStorage.booksUser){
                let lenName = JSON.parse(sessionStorage.booksUser);
                for(let n = 0;n < lenName.length; n++){
                    if(lenName[n].num == this.inputRegNum){
                        this.$Modal.warning({
                            title: "title",
                            content: "该用户已经注册"
                        });
                        return;
                    }
                }
            }
            if(this.inputPass1 != this.inputPass2){
                this.$Modal.warning({
                    title: "title",
                    content: "两次密码不一致"
                });
                return;
            }
            let user = {
                num: this.inputRegNum,
                name: this.inputRegName,
                passFir: this.inputPass1,
                passLas: this.inputPass2,
                date:'',
            };
            user.date = new Date().toLocaleDateString();
            let regUser = null;
            if(sessionStorage.booksUser){
                regUser = JSON.parse(sessionStorage.booksUser);
            }else{
                regUser = [];
            }
            regUser.push(user);
            sessionStorage.booksUser = JSON.stringify(regUser);
            sessionStorage.isLogin = JSON.stringify(this.inputRegName);
            this.$Modal.success({
                title: "title",
                content: "注册成功"
            });
            this.$emit('reg');
        },
        /* 点击登陆 */
        log(){
            if(this.inputNum == '' || this.inputPass == ''){
                this.$Modal.warning({
                    title: "title",
                    content: "请输入账号和密码"
                });
                return;
            }
            if(sessionStorage.booksUser){
                let logUser = JSON.parse(sessionStorage.booksUser);
                for(let i = 0; i < logUser.length; i++){
                    if(logUser[i].num == this.inputNum && logUser[i].passFir == this.inputPass){
                        this.$Modal.success({
                            title: "title",
                            content: "登陆成功"
                        });
                        this.$emit('log');
                        sessionStorage.isLogin = JSON.stringify(logUser[i].name);
                        this.$store.dispatch('changeLoginState', true);
                        break;
                    }
                }
            }else{
                this.$Modal.warning({
                    title: "title",
                    content: "没有账号，请注册"
                });
            }
        }

    }
}
</script>
<style scoped>
    .login-box{
        width:100%;
        position: relative;
    }
    .login-title{
        width:100%;
        height: 50px;
        display: flex;
        justify-content: space-around;
        border-bottom: 1px solid #ccc;
        box-sizing: border-box;
        line-height: 50px;
    }
    .login-title>span{
        display: inline-block;
        width:30%;
        height: 99%;
        font-size: 22px;
    }
    .login-title>span.border{
        border-bottom:2px solid rgb(8, 85, 8);
    }
    .input{
        width:100%;
        position: absolute;
        left:0;
        top:70px;
       
    }
    .input>div{
        position: relative;
    }
    .input>div i{
        position: absolute;
        left:5%;
        top:50%;
        transform: translateY(-50%);
        font-size: 20px;
        color:#666;
    }
    .input input{
        width:calc(100% - 10%);
        height: 50px;
        border:none;
        outline: none;
        border-bottom: 1px solid #eee;
        text-indent: 10%;
        letter-spacing: 2px;
        color:#333;
    }
    .input button{
        margin-top: 20px;
        width:calc(100% - 10%);
        height: 50px;
        color:#fff;
        border:none;
        border-radius: 5px;
        background-color: green;
        outline: none;
    }
</style>



