<template>
    <div class="page shopping">
        <div class="tips">
            <span class="allCheck" v-show="isEdit" @click="allCheck()"><input type="checkbox"/>全选</span>
            <span @click="editBtn()" v-show="nonEdit">编辑</span>
            <span class="cancelBtn" v-show="isEdit" @click="cancelBtn()">取消</span>
            <span class="delBtn" v-show="isEdit" @click="delBtn()">删除选中</span>
        </div>
        <div class="not-shopping" v-if="isShow"><p>{{notData}}</p></div>
        <div class="content"  v-else v-for="(item, index) in books" :key="index">
            <div class="con-box">
                <div class="radio-box" v-show="isEdit">
                    <input type="checkbox" class="radio" @click="Sec(index,books)"/>
                </div>
                <div class="left">
                <img :src="item.image" alt=""/>
                </div>
                <div class="right" :style="changeWidth">
                    <p>{{item.name}}</p>
                    <p>{{item.author}}</p>
                    <p>￥ {{(item.price * item.num).toFixed(2)}}</p>
                    <div class="counter" v-show="!isEdit">
                        <span class="btn" @click="down(item,index)">-</span>
                        <input class="input" type="number" :value="item.num">
                        <span class="btn" @click="up(item,index)">+</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="totalPrice"  v-show="nonEdit">总计：{{total}}</div>
        <button type="button" class="goBuyBtn" v-show="nonEdit" @click="payment">前往支付</button>
    </div>
</template>
<script>
export default{
    name:'shopping',
    data(){
        return{
            books:[],
            notData:'',
            isShow:false,
            isEdit:false,
            // isSec:false,
            changeWidth:{
                width:""
            },
            selected:{
                background: '#000'
            },
            inputNum:'1',
            totalPrice:0,
            isAll:false,
            nonEdit:true,
            isDel:false
        }
    },
    created(){
        if(localStorage.books && JSON.parse(localStorage.books).length > 0){
            this.books = JSON.parse(localStorage.books);
            this.isShow = false;
        }else if(!localStorage.books || JSON.parse(localStorage.books).length <= 0){
            this.books = [];
            this.notData = "亲，您的购物车里还没有物品哦，赶紧去逛逛吧";
            this.isShow = true;
            this.nonEdit = false;
        }
    },
    computed:{
        total(){
            this.totalPrice = 0;
            this.books.forEach((item, index)=>{
                this.totalPrice += parseFloat(item.num * item.price);
            });
            return this.totalPrice.toFixed(2);
        }
    },
    beforeRouteEnter (to, from, next) {
    // just use `this`
    if(to.name){
        if(!sessionStorage.isLogin){
            alert('请先登录')
            return;
        }
        next()
    }
    
    },
    methods:{
        editBtn(){
            this.isEdit = true;
            this.changeWidth.width = "70%";
            this.nonEdit = false;
        },
        cancelBtn(){
            this.isEdit = false;
            this.nonEdit = true;
             this.changeWidth.width = "";
        },
        Sec(index,books){
            this.isAllDel = false;
        },
        /* 全选和全不选 */
        allCheck(){
            // 判断checkbox选中状态，返回true或者false
            let isCheckAll = $(".allCheck input[type='checkbox']").is(':checked');  
            if (!isCheckAll) {  
                $(".radio-box input[type='checkbox']").each(function() {  
                    this.checked = false;  
                });  
                isCheckAll = false;  
                this.isAll = false;
                
            } else {  
                $(".radio-box input[type='checkbox']").each(function() {  
                    this.checked = true;  
                });  
                isCheckAll = true;  
                this.isAll = true;
            }  
        },
        /* 删除 */
        delBtn(){
            $('.radio-box').each((index,item)=>{
                let check = $(".radio-box input[type='checkbox']").eq(index).is(':checked');
                if(check){
                    if(this.isAll){
                        this.isDel = true;
                        $('.con-box').remove();
                        localStorage.removeItem('books');
                        if(!localStorage.books){
                            this.nonEdit = false;
                            this.isEdit = false;
                            this.isShow = true;
                            this.notData = "亲，您的购物车里还没有物品哦，赶紧去逛逛吧";
                        }
                    }else{
                        this.isDel = true;
                        let booksArr = [];
                        let newIndex = $('.radio').index();
                        booksArr = this.books.splice(newIndex, 1)
                        localStorage.books = JSON.stringify(this.books);
                        if(JSON.parse(localStorage.books) == ''){
                            this.nonEdit = false;
                            this.isEdit = false;
                            this.isShow = true;
                            this.notData = "亲，您的购物车里还没有物品哦，赶紧去逛逛吧";
                        }
                    }
                }
            });
            if(this.isDel){
                this.$Modal.success({
                    title: "title",
                    content: "删除成功"
                });
            }
        },
        /* 数量加一 */
        up(item, index){
            item.num += 1;
            if(item.num > 5){
                item.num = 5;
            }
            var localBooks = JSON.parse(localStorage.books);
            for(var i = 0; i < localBooks.length; i++){
                if(localBooks[i].name == item.name){
                    localBooks[i].num += 1;
                    if(localBooks[i].num > 5){
                        localBooks[i].num = 5;
                    }
                }
            }
            localStorage.books = JSON.stringify(localBooks);
        },
        /* 数量减一 */
        down(item, index){
            item.num -= 1;
            if(item.num < 1){
                item.num = 1;
            }
            var localBooks = JSON.parse(localStorage.books);
            for(var j = 0; j < localBooks.length; j++){
                if(localBooks[j].name == item.name){
                    localBooks[j].num -= 1;
                    if(localBooks[j].num < 1){
                        localBooks[j].num = 1;
                    }
                }
            }
            localStorage.books = JSON.stringify(localBooks);
        },
        payment(){
            // this.$router.push('./settlement');
        }
    }
}
</script>
<style scoped>
    .shopping{
        background-color: rgba(238, 238, 238,0.5);
    }
    .tips{
        width:100%;
        height: 40px;
        text-align: right;
        padding-right:10px;
        line-height: 40px;
    }
    .tips>span:not(:nth-child(1)){
        background-color: #000;
        color:#fff;
        padding:5px 10px;
        border-radius: 5px;
    }
    .allCheck{
        padding-left:10px;
        float: left;
        vertical-align: top;
    }
    .allCheck input{
        width:15px;
        height: 15px;
        position: relative;
        top:3px;
    }
    .not-shopping{
        height: 200px;
        background: url('https://shopstatic.vivo.com.cn/vivoshop/web/dist/img/prod/shopping-cart_50507a3.png') no-repeat center top;
        background-size: 50%;
        position: relative;
    }
    .not-shopping p{
        position: absolute;
        bottom: 0;
        left:50%;
        transform: translateX(-50%);
        font-size: 12px;
        color: #333;
    }
    .content{
        width:100%;
        
    }
    .con-box{
        padding:10px;
        width:100%;
        height: 120px;
        border-bottom:1px solid #ddd;
        font-size: 0;
        background-color: #fff;
        
    }
    
    .radio-box,.left,.right{
        display: inline-block;
    }
    .radio-box{
        width:10%;
        margin-left: -20px;
        height: 100px;
        position: relative;
    }
    .radio-box input{
        position: absolute;
        top:50%;
        left:50%;
        width:15px;
        height: 15px;
        transform: translate(-50%,-50%);
        border-radius: 50%;
    }
    .radio-cur{
        border:1px solid #999;  
    }
    .radio{
        border:none;
    }
    .left{
        width:20%;
        height: 100px;
        overflow: hidden;
    }
    .left img{
        width:100%;
    }
    .right{
        width:80%;
        height: 100px;
        font-size: 16px;
        vertical-align: top;
        text-align: left;
        padding-left:10px;
        position: relative;
    }
    .right p:nth-child(1){
        margin-top: 10px;
        font-size: 14px;
        color:rgb(81, 81, 219);
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .right p:nth-child(2){
        margin: 5px 0;
        font-size: 13px;
        
    }
    .right p:nth-child(3){
        margin: 10px 0;
        font-size: 20px;
        color:rgb(122, 9, 9);
    }
    .counter{
        position: absolute;
        right:10px;
        bottom:5px;
        width:85px;
        height: 30px;
        font-size: 0;
    }
    .counter::after{
        content: '数量上限5本';
        position: absolute;
        left:0;
        bottom: -15px;
        font-size: 12px;
        transform: scale(0.8);
        font-weight: lighter;
        color:#666;
    }
    .btn{
        display: inline-block;
        width:25px;
        height: 25px;
        border:1px solid #888;
        font-size: 16px;
        vertical-align: middle;
        text-align: center;
    }
    .input{
        width:30px;
        height: 25px;
        border:none;
        border-top:1px solid #888;
        border-bottom:1px solid #888;
        font-size: 12px;
        vertical-align: middle;
        text-align: center;
        outline: none;
    }
    .totalPrice{
        margin-top: 20px;
        font-size: 20px;
        color:rgb(173, 26, 26);
        text-align: right;
        padding-right:20px;
        width:100%;
    }
    .goBuyBtn{
        margin: 20px auto 40px;
        width:calc(100% - 20px);
        height:50px;
        border:none;
        outline: none;
        border-radius: 5px;
        background-color: rgb(16, 114, 16);
        color:#fff;
    }
</style>



