<template>
    <div class="page classify">
        <div class="search">
            <Icon type="md-search" @click="search()" />
            <input type="search" placeholder="请输入您想要查找的书" @keyup.enter="search()" v-model="keyword">
        </div>
        <div class="sort">
            <span @click="rise" v-show="isSort">升序<Icon type="md-arrow-up" /></span>
            <span @click="drop" v-show="!isSort">降序<Icon type="md-arrow-down" /> </span>
        </div>
        <div class="content" v-for="(item,index) in books" :key="index">
            <div class="con-box" @click="classifyBtn(index)">
                <div class="left">
                    <img :src="item.cover">
                </div>
                <div class="right">
                    <p>{{item.name}}</p>
                    <p>{{item.author}}</p>
                    <p>{{item.price}}</p>
                </div>
            </div>
        </div>
        <Detail v-if="isDetail" :detData="detIndex" @goBack="goback()"/>
    </div>
</template>
<script>
import Detail from './detail';
export default{
    name:'classify',
    components:{
        Detail
    },
    data(){
        return{
            books:'',
            detIndex:'',
            isDetail:false,
            isSort:false,
            keyword:'',
            bookShow:''
        }
    },
    created(){
        let url = 'book-info.json';
        this.$axios.get(url, {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
        }).then(res => {
            this.books = res.data;
            this.bookShow = res.data;
        })
    },
    methods:{
        /* 给详情页传递url和参数 */
        classifyBtn(index){
            this.detIndex = {name:"classify",index,url:'book-info.json'};
            this.isDetail = true;
        },
        /* 返回关闭详情页 */
        goback(){
            this.isDetail = false;
        },
        /* 升序 */
        rise(){
            this.isSort = false;
            this.books.sort(function(n1,n2){
               return n1.price - n2.price;
            }).reverse();
        },
        /* 降序 */
        drop(){
            this.isSort = true;
            this.books.sort(function(n1,n2){
               return n1.price - n2.price;
            });
        },
        /* 搜索 */
        search(){
            let bookArr = [];
            for(let i = 0; i < this.books.length; i++){
                let reg = new RegExp(this.keyword, "i");
                if((reg.test(this.books[i].name) || reg.test(this.books[i].price)) && this.keyword != ''){
                    bookArr.push(this.books[i]);
                }
            }
            if(this.keyword != ''){
                this.books = bookArr;
            }else{
                this.books = this.bookShow;
            }
        }
    }
}
</script>
<style scoped>
    .search{
        position: relative;
        padding:10px 0;
        width:100%;
    }
    .search i{
        position: absolute;
        top:50%;
        left:10%;
        transform: translateY(-50%);
        font-size: 20px;
    }
    .search input{
        width:calc(100% - 10%);
        height: 40px;
        border-radius: 40px;
        outline: none;
        border:1px solid #ccc;
        text-indent: 12%;
    }
    .sort{
        text-align: right;
        position: relative;
        width:100%;
        height: 20px;
    }
    .sort span{
        position: absolute;
        right:20px;
        top:50%;
        transform: translateY(-50%);
        padding:1px 6px;
        color:#333;
    }
    .content{
        width:100%;
        
    }
    .con-box{
        padding:10px;
        width:100%;
        height: 120px;
        border-bottom:1px solid #ddd;
        font-size: 0;
        background-color: #fff;
    }
    .left,.right{
        display: inline-block;
    }
    .left{
        width:20%;
        height: 100px;
        overflow: hidden;
    }
    .left img{
        width:100%;
    }
    .right{
        width:80%;
        height: 100px;
        font-size: 16px;
        vertical-align: top;
        text-align: left;
        padding-left:10px;
        position: relative;
    }
    .right p:nth-child(1){
        margin-top: 10px;
        font-size: 14px;
        color:rgb(81, 81, 219);
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .right p:nth-child(2){
        margin: 5px 0;
        font-size: 13px;
        
    }
    .right p:nth-child(3){
        margin: 10px 0;
        font-size: 20px;
        color:rgb(122, 9, 9);
    }
</style>



